body{
    margin: 0;
    font-family: sans-serif;
    width:100%;
}


.header{
    width: 70%;
    text-align: center;
    background-color: royalblue;
    height: 50px;
    font-size: 2em;
    color: white;
    padding: 20px;
    margin:0 auto;

}

.todoform{
    width: 50%;
    margin:20px auto;
    display:block;
    text-align:center;
}

.todoinput{
    width: 70%;
    border: none;
    border-bottom:1px solid whitesmoke;
    font-size: 1.5 em;
    text-align: center;
    height: 50px;
    margin:0 auto;
}

.todobutton{
    width: 70%;
    border: none;
    height: 50px;
    background-color: tomato;
    font-size: 1.2 em;
    color: white;
    margin-top:20px;



}
.todoitem{
margin:0 auto;
text-align:center;
border: 2px solid white;
padding: 20px;
margin-top:20px;
}

.deleteButton{
  
    width: 100px;
    border: none;
    height: 50px;
    background-color: tomato;
    color: white;
    border-radius: 5px;
}











